import * as React from 'react';
// import CategoryComponent from './CategoryComponent';
import GistViewer from './components/GistViewer';

interface Author {
  name: string;
  image: string;
  link: string;
  position: string;
}

interface Category {
  name: string;
}
interface Blog {
  id: string;
  title: string;
  author: Author;
  date: string;
  categories: Category[];
  link: string;
  description: string;
  images: string[];
}

// Define the props interface for ChildComponent
interface ChildComponentProps {
  blog: Blog;
}

const Blog: React.VFC<ChildComponentProps> = ({ blog }) => {
  return (
    <div className="App">
      <header className="App-header">
        <h1>GitHub Gist Viewer</h1>
        <GistViewer gistId={blog.id} />
      </header>
    </div>
  );
};
export default Blog;
