import * as React from 'react';
import { useState, useEffect } from 'react';
import axios from 'axios';
import { RouteComponentProps } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';

interface GistFile {
  filename: string;
  type: string;
  language: string;
  raw_url: string;
  size: number;
  content: string;
}

interface Gist {
  description: string;
  files: { [key: string]: GistFile };
}

interface MatchParams {
  gistId: string;
}

interface GistViewerProps extends RouteComponentProps<MatchParams> {}

const GistViewer: React.FC<GistViewerProps> = ({ match }) => {
  const { gistId } = match.params;
  const [gist, setGist] = useState<Gist | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchGist = async () => {
      try {
        const response = await axios.get<Gist>(
          `https://api.github.com/gists/${gistId}`,
        );
        setGist(response.data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchGist();
  }, [gistId]);

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  const markdownFile =
    gist &&
    Object.values(gist.files).find((file) => file.language === 'Markdown');

  return (
    <div>
      <h1>{gist?.description}</h1>
      {markdownFile ? (
        <ReactMarkdown>{markdownFile.content || ''}</ReactMarkdown>
      ) : (
        <div>No markdown file found in this gist.</div>
      )}
    </div>
  );
};

export default GistViewer;
